#cartEmptyContainer {
    margin: 20px 20px 20px 20px;
}

#containerName {
    margin-right: 20px;
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: right;
}

#containerName p {
    margin: 0;
    padding: 10px;
    color: white;
    border: 1px solid #2b519c;
    font-size: 20px;
    font-weight: 600;
    border-radius: 10px;
    background-color: #2b519c;

}

#cartEmptyContentContainer {
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#cartContentContainer img {
    margin-right: 20px;   
}


#cartContentContainer p {
    margin: 0;
    padding: 10px;
    color: #2b519c;
    font-size: 20px;
    font-weight: 600;
}